<template>
  <div class="pdf-watermark">
    <div class="watermark-container">
      <!-- PDF Preview Section -->
      <div class="pdf-preview-section">
        <el-card class="preview-card">
          <template #header>
            <div class="card-header">
              <span>PDF Preview</span>
              <div class="preview-controls">
                <el-button-group>
                  <el-button 
                    :disabled="currentPage <= 1" 
                    @click="prevPage"
                    size="small"
                  >
                    Previous
                  </el-button>
                  <el-button size="small">
                    {{ currentPage }} / {{ totalPages }}
                  </el-button>
                  <el-button 
                    :disabled="currentPage >= totalPages" 
                    @click="nextPage"
                    size="small"
                  >
                    Next
                  </el-button>
                </el-button-group>
                <el-button 
                  @click="resetZoom" 
                  size="small"
                  style="margin-left: 10px;"
                >
                  Reset Zoom
                </el-button>
              </div>
            </div>
          </template>
          
          <div class="pdf-upload-area" v-if="!pdfDocument">
            <el-upload
              ref="uploadRef"
              :auto-upload="false"
              :limit="1"
              :on-change="handleFileChange"
              :before-upload="beforeUpload"
              accept=".pdf"
              drag
              class="pdf-upload"
            >
              <el-icon class="el-icon--upload"><upload-filled /></el-icon>
              <div class="el-upload__text">
                Drag PDF file here, or <em>click to upload</em>
              </div>
              <template #tip>
                <div class="el-upload__tip">
                  Only PDF files allowed, max 100MB
                </div>
              </template>
            </el-upload>
          </div>
          
          <div v-else class="pdf-viewer-container">
            <!-- PDF Preview Area -->
            <div 
              ref="pdfViewer" 
              class="pdf-viewer"
              @mousedown="handleMouseDown"
              @mousemove="handleMouseMove"
              @mouseup="handleMouseUp"
              @mouseleave="handleMouseUp"
            >
              <canvas ref="pdfCanvas" class="pdf-canvas" v-if="pdfDocument"></canvas>
              
              <!-- Watermark Preview Layer -->
              <!-- Text Watermark Preview -->
              <div 
                v-if="form.watermarkType === 'text' && form.watermarkText && pdfDocument"
                class="watermark-preview text-watermark"
                :style="watermarkStyle"
                @mousedown="startDrag"
              >
                {{ form.watermarkText }}
              </div>
              
              <!-- Image Watermark Preview -->
              <div 
                v-if="form.watermarkType === 'image' && watermarkFile && pdfDocument"
                class="watermark-preview image-watermark"
                :style="imageWatermarkStyle"
                @mousedown="startDrag"
              >
                <img 
                  :src="watermarkImageUrl" 
                  :alt="'Watermark Image'"
                  class="watermark-image"
                />
              </div>
            </div>
            
            <!-- Fallback Preview Hint -->
            <div v-if="!pdfDocument" class="pdf-placeholder">
              <el-icon size="48"><Document /></el-icon>
              <p>PDF preview loading...</p>
              <p class="placeholder-tip">If it takes too long to load, please refresh the page and try again</p>
              <el-button @click="retryLoadPdf" type="primary" size="small" style="margin-top: 10px;">
                Retry Load
              </el-button>
            </div>
          </div>
        </el-card>
      </div>

      <!-- Watermark Settings Panel -->
      <div class="watermark-settings-section">
        <el-card class="settings-card">
          <template #header>
            <div class="card-header">
              <span>Watermark Settings</span>
              <el-icon><Edit /></el-icon>
            </div>
          </template>
          
          <el-form :model="form" label-width="100px" size="small">
            <el-form-item label="Watermark Type" required>
              <el-radio-group v-model="form.watermarkType" @change="handleWatermarkTypeChange">
                <el-radio label="text">Text Watermark</el-radio>
                <el-radio label="image">Image Watermark</el-radio>
              </el-radio-group>
            </el-form-item>

            <!-- Text Watermark Settings -->
            <template v-if="form.watermarkType === 'text'">
              <el-form-item label="Watermark Text" required>
                <el-input
                  v-model="form.watermarkText"
                  placeholder="Enter watermark text"
                  clearable
                />
              </el-form-item>

              <el-form-item label="Text Color">
                <el-color-picker v-model="form.textColor" />
                <span class="color-preview" :style="{ backgroundColor: form.textColor }"></span>
              </el-form-item>

              <el-form-item label="Font Size">
                <el-slider
                  v-model="form.fontSize"
                  :min="12"
                  :max="120"
                  :step="2"
                  show-input
                  :format-tooltip="formatFontSize"
                />
              </el-form-item>

              <el-form-item label="Text Angle">
                <el-slider
                  v-model="form.rotation"
                  :min="0"
                  :max="360"
                  :step="15"
                  show-input
                  :format-tooltip="formatRotation"
                />
              </el-form-item>

              <el-form-item label="Opacity">
                <el-slider
                  v-model="form.opacity"
                  :min="0.1"
                  :max="1"
                  :step="0.1"
                  show-input
                  :format-tooltip="formatOpacity"
                />
              </el-form-item>
            </template>

            <!-- Image Watermark Settings -->
            <template v-if="form.watermarkType === 'image'">
              <el-form-item label="Watermark Image" required>
                <el-upload
                  ref="watermarkUploadRef"
                  :auto-upload="false"
                  :limit="1"
                  :on-change="handleWatermarkFileChange"
                  :before-upload="beforeWatermarkUpload"
                  accept=".jpg,.jpeg,.png,.bmp,.tiff"
                  drag
                >
                  <el-icon class="el-icon--upload"><upload-filled /></el-icon>
                  <div class="el-upload__text">
                    Drag watermark image here, or <em>click to upload</em>
                  </div>
                  <template #tip>
                    <div class="el-upload__tip">
                      Supports JPG, PNG, BMP, TIFF format images
                    </div>
                  </template>
                </el-upload>
              </el-form-item>
            </template>

            <el-form-item>
              <el-button
                type="primary"
                :loading="loading"
                :disabled="!canSubmit"
                @click="handleSubmit"
                size="small"
              >
                {{ loading ? 'Processing...' : 'Add Watermark' }}
              </el-button>
              <el-button @click="resetForm" size="small">Reset</el-button>
            </el-form-item>
          </el-form>

          <!-- Results Display -->
          <div v-if="result" class="result-section">
            <el-divider>Processing Results</el-divider>
            <el-alert
              :title="result.msg"
              type="success"
              :closable="false"
              show-icon
            />
            <div class="result-info">
              <p><strong>Task ID:</strong> {{ result.data.taskId }}</p>
              <p><strong>Processed File:</strong> {{ result.data.watermarkedFile }}</p>
              <el-button type="success" @click="downloadResult" size="small">
                <el-icon><Download /></el-icon>
                Download Result
              </el-button>
            </div>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted, nextTick } from 'vue'
import { ElMessage } from 'element-plus'
import { Edit, UploadFilled, Download, Document } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { validateFile, FILE_TYPES } from '@/utils/file'

// Use CDN version of PDF.js to avoid compatibility issues
let pdfjsLib = null

// Async load PDF.js
const loadPdfJs = async () => {
  if (!pdfjsLib) {
    // Check if PDF.js is already loaded
    if (window.pdfjsLib) {
      pdfjsLib = window.pdfjsLib
      pdfjsLib.GlobalWorkerOptions.workerSrc = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.11.338/pdf.worker.min.js'
      return pdfjsLib
    }
    
    // Dynamically load PDF.js CDN version - use stable 2.11.338 version
    const script = document.createElement('script')
    script.src = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.11.338/pdf.min.js'
    script.async = true
    
    return new Promise((resolve, reject) => {
      script.onload = () => {
        pdfjsLib = window.pdfjsLib
        // Set PDF.js worker
        pdfjsLib.GlobalWorkerOptions.workerSrc = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.11.338/pdf.worker.min.js'
        resolve(pdfjsLib)
      }
      script.onerror = (error) => {
        console.error('PDF.js load failed:', error)
        reject(error)
      }
      document.head.appendChild(script)
    })
  }
  return pdfjsLib
}

const uploadRef = ref()
const watermarkUploadRef = ref()
const pdfCanvas = ref()
const pdfViewer = ref()
const loading = ref(false)
const selectedFile = ref(null)
const watermarkFile = ref(null)
const watermarkImageUrl = ref('')
const result = ref(null)

// PDF related state
const pdfDocument = ref(null)
const currentPage = ref(1)
const totalPages = ref(0)
const scale = ref(1.0)
const isDragging = ref(false)
const dragStart = ref({ x: 0, y: 0 })
const watermarkPosition = ref({ x: 50, y: 50 }) // Watermark position (percentage)

const form = reactive({
  watermarkType: 'text',
  watermarkText: '',
  position: 'center',
  opacity: 0.5,
  textColor: '#FF0000',
  fontSize: 48,
  rotation: 0,
  offsetX: 0,
  offsetY: 0
})

// Calculate if can submit
const canSubmit = computed(() => {
  if (!selectedFile.value) return false
  if (form.watermarkType === 'text' && !form.watermarkText.trim()) return false
  if (form.watermarkType === 'image' && !watermarkFile.value) return false
  return true
})

// Watermark style computed property
const watermarkStyle = computed(() => {
  if (form.watermarkType !== 'text' || !form.watermarkText) return {}
  
  return {
    position: 'absolute',
    left: `${watermarkPosition.value.x}%`,
    top: `${watermarkPosition.value.y}%`,
    transform: `translate(-50%, -50%) rotate(${form.rotation}deg)`,
    color: form.textColor,
    fontSize: `${form.fontSize}px`,
    opacity: form.opacity,
    pointerEvents: 'auto',
    userSelect: 'none',
    whiteSpace: 'nowrap',
    zIndex: 10,
    fontFamily: 'Arial, sans-serif',
    fontWeight: 'bold',
    textShadow: '1px 1px 2px rgba(0,0,0,0.3)',
    cursor: 'move'
  }
})

// Image watermark style computed property
const imageWatermarkStyle = computed(() => {
  if (form.watermarkType !== 'image' || !watermarkFile.value) return {}
  
  return {
    position: 'absolute',
    left: `${watermarkPosition.value.x}%`,
    top: `${watermarkPosition.value.y}%`,
    transform: `translate(-50%, -50%) rotate(${form.rotation}deg)`,
    opacity: form.opacity,
    pointerEvents: 'auto',
    userSelect: 'none',
    zIndex: 10,
    cursor: 'move',
    maxWidth: '200px',
    maxHeight: '200px'
  }
})

// Watermark type change handling
const handleWatermarkTypeChange = () => {
  form.watermarkText = ''
  watermarkFile.value = null
  watermarkImageUrl.value = ''
  watermarkUploadRef.value?.clearFiles()
}

// File selection handling
const handleFileChange = async (file) => {
  const validation = validateFile(file.raw, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    uploadRef.value.clearFiles()
    return
  }
  selectedFile.value = file.raw
  await loadPdf(file.raw)
}

// Load PDF file
const loadPdf = async (file) => {
  try {
    // Ensure PDF.js is loaded
    const pdfjs = await loadPdfJs()
    
    if (!pdfjs) {
      throw new Error('PDF.js library load failed')
    }
    
    const arrayBuffer = await file.arrayBuffer()
    const pdf = await pdfjs.getDocument(arrayBuffer).promise
    
    pdfDocument.value = pdf
    totalPages.value = pdf.numPages
    currentPage.value = 1
    
    await renderPage(1)
    ElMessage.success('PDF loaded successfully')
  } catch (error) {
    console.error('PDF load failed:', error)
    if (error.message.includes('PDF.js library load failed')) {
      ElMessage.error('PDF preview function temporarily unavailable, please try again later')
    } else {
      ElMessage.error('PDF load failed, please check file format')
    }
  }
}

// Render PDF page
const renderPage = async (pageNum) => {
  if (!pdfDocument.value) return
  
  try {
    const page = await pdfDocument.value.getPage(pageNum)
    const canvas = pdfCanvas.value
    const context = canvas.getContext('2d')
    
    // Calculate zoom ratio
    const viewport = page.getViewport({ scale: scale.value })
    canvas.height = viewport.height
    canvas.width = viewport.width
    
    // Render page
    const renderContext = {
      canvasContext: context,
      viewport: viewport
    }
    
    await page.render(renderContext).promise
  } catch (error) {
    console.error('Page render failed:', error)
    ElMessage.error('Page render failed')
  }
}

// Previous page
const prevPage = async () => {
  if (currentPage.value > 1) {
    currentPage.value--
    await renderPage(currentPage.value)
  }
}

// Next page
const nextPage = async () => {
  if (currentPage.value < totalPages.value) {
    currentPage.value++
    await renderPage(currentPage.value)
  }
}

// Reset zoom
const resetZoom = async () => {
  scale.value = 1.0
  await renderPage(currentPage.value)
}

// Retry load PDF
const retryLoadPdf = async () => {
  if (selectedFile.value) {
    // Clear current state
    pdfDocument.value = null
    pdfjsLib = null
    window.pdfjsLib = null
    
    // Clear loaded script
    const existingScript = document.querySelector('script[src*="pdf.min.js"]')
    if (existingScript) {
      existingScript.remove()
    }
    
    // Reload
    await loadPdf(selectedFile.value)
  }
}

// Drag related methods
const startDrag = (event) => {
  event.preventDefault()
  isDragging.value = true
  dragStart.value = {
    x: event.clientX,
    y: event.clientY
  }
  document.addEventListener('mousemove', handleDrag)
  document.addEventListener('mouseup', stopDrag)
}

const handleDrag = (event) => {
  if (!isDragging.value) return
  
  const rect = pdfViewer.value.getBoundingClientRect()
  const x = ((event.clientX - rect.left) / rect.width) * 100
  const y = ((event.clientY - rect.top) / rect.height) * 100
  
  // Limit within PDF area
  watermarkPosition.value = {
    x: Math.max(0, Math.min(100, x)),
    y: Math.max(0, Math.min(100, y))
  }
}

const stopDrag = () => {
  isDragging.value = false
  document.removeEventListener('mousemove', handleDrag)
  document.removeEventListener('mouseup', stopDrag)
}

// Mouse event handling (for PDF canvas drag)
const handleMouseDown = (event) => {
  if ((form.watermarkType === 'text' && form.watermarkText) || 
      (form.watermarkType === 'image' && watermarkFile.value)) {
    startDrag(event)
  }
}

const handleMouseMove = (event) => {
  if (isDragging.value) {
    handleDrag(event)
  }
}

const handleMouseUp = () => {
  stopDrag()
}

// Watermark file selection handling
const handleWatermarkFileChange = (file) => {
  const validation = validateFile(file.raw, 'IMAGE')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    watermarkUploadRef.value.clearFiles()
    return
  }
  watermarkFile.value = file.raw
  
  // Create image preview URL
  const reader = new FileReader()
  reader.onload = (e) => {
    watermarkImageUrl.value = e.target.result
  }
  reader.readAsDataURL(file.raw)
}

// Pre-upload validation
const beforeUpload = (file) => {
  const validation = validateFile(file, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // Prevent auto-upload
}

// Watermark file pre-upload validation
const beforeWatermarkUpload = (file) => {
  const validation = validateFile(file, 'IMAGE')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // Prevent auto-upload
}

// Opacity formatting
const formatOpacity = (value) => {
  return `${Math.round(value * 100)}%`
}

// Font size formatting
const formatFontSize = (value) => {
  return `${value}px`
}

// Angle formatting
const formatRotation = (value) => {
  return `${value}°`
}

// Submit processing
const handleSubmit = async () => {
  if (!canSubmit.value) {
    ElMessage.warning('Please complete all required fields')
    return
  }

  loading.value = true
  try {
    const formData = new FormData()
    formData.append('file', selectedFile.value)
    formData.append('watermarkType', form.watermarkType)
    formData.append('opacity', form.opacity.toString())

    if (form.watermarkType === 'text') {
      formData.append('watermarkText', form.watermarkText)
      formData.append('textColor', form.textColor)
      formData.append('fontSize', form.fontSize.toString())
      formData.append('rotation', form.rotation.toString())
      
      // Use drag position instead of preset position
      formData.append('position', 'custom')
      formData.append('offsetX', watermarkPosition.value.x.toString())
      formData.append('offsetY', watermarkPosition.value.y.toString())
    } else if (form.watermarkType === 'image' && watermarkFile.value) {
      // Image watermark: add image file
      formData.append('watermarkFile', watermarkFile.value)
      formData.append('position', 'custom')
      formData.append('offsetX', watermarkPosition.value.x.toString())
      formData.append('offsetY', watermarkPosition.value.y.toString())
      formData.append('rotation', form.rotation.toString())
    }

    const response = await pdfApi.addWatermark(formData)
    result.value = response
    ElMessage.success('PDF watermark added successfully')
  } catch (error) {
    console.error('PDF watermark add failed:', error)
    ElMessage.error('PDF watermark add failed, please try again')
  } finally {
    loading.value = false
  }
}

// Download result
const downloadResult = () => {
  if (result.value?.data?.downloadUrl) {
    try {
      const { downloadFile } = require('@/utils/download')
      downloadFile(result.value.data.downloadUrl, result.value.data.watermarkedFile)
      ElMessage.success('Download started')
    } catch (error) {
      console.error('Download failed:', error)
      ElMessage.error('Download failed, please try again')
    }
  } else {
    ElMessage.warning('No file available for download')
  }
}

// Reset form
const resetForm = () => {
  form.watermarkType = 'text'
  form.watermarkText = ''
  form.position = 'center'
  form.opacity = 0.5
  form.textColor = '#FF0000'
  form.fontSize = 48
  form.rotation = 0
  form.offsetX = 0
  form.offsetY = 0
  selectedFile.value = null
  watermarkFile.value = null
  watermarkImageUrl.value = ''
  result.value = null
  pdfDocument.value = null
  currentPage.value = 1
  totalPages.value = 0
  watermarkPosition.value = { x: 50, y: 50 }
  uploadRef.value?.clearFiles()
  watermarkUploadRef.value?.clearFiles()
}
</script>

<style scoped>
.pdf-watermark {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

.watermark-container {
  display: flex;
  gap: 20px;
  height: calc(100vh - 100px);
}

.pdf-preview-section {
  flex: 2;
  min-width: 0;
}

.watermark-settings-section {
  flex: 1;
  min-width: 300px;
}

.preview-card,
.settings-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.preview-card .el-card__body {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0;
}

.settings-card .el-card__body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
}

.preview-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.pdf-upload-area {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
}

.pdf-upload {
  width: 100%;
}

.pdf-viewer-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: auto;
  background-color: #f5f5f5;
  padding: 20px;
}

.pdf-viewer {
  position: relative;
  display: inline-block;
  margin: 0 auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
  cursor: grab;
}

.pdf-viewer:active {
  cursor: grabbing;
}

.pdf-canvas {
  display: block;
  max-width: 100%;
  height: auto;
}

.watermark-preview {
  position: absolute;
  pointer-events: auto;
  cursor: move;
  user-select: none;
  z-index: 10;
  border: 2px dashed #409eff;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.8);
  transition: all 0.2s ease;
}

.watermark-preview:hover {
  border-color: #66b1ff;
  background-color: rgba(255, 255, 255, 0.9);
}

/* Text watermark style */
.text-watermark {
  padding: 4px 8px;
}

/* Image watermark style */
.image-watermark {
  padding: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.watermark-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 2px;
}

.result-section {
  margin-top: 20px;
}

.result-info {
  margin-top: 15px;
  padding: 15px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.result-info p {
  margin: 8px 0;
  font-size: 14px;
}

.color-preview {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-left: 10px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  vertical-align: middle;
}

/* Responsive design */
@media (max-width: 1200px) {
  .watermark-container {
    flex-direction: column;
    height: auto;
  }
  
  .pdf-preview-section,
  .watermark-settings-section {
    flex: none;
  }
  
  .pdf-viewer-container {
    height: 500px;
  }
}

/* Scrollbar style */
.pdf-viewer-container::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.pdf-viewer-container::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.pdf-viewer-container::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.pdf-viewer-container::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* PDF placeholder style */
.pdf-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #909399;
  text-align: center;
  padding: 40px;
}

.pdf-placeholder .el-icon {
  margin-bottom: 16px;
  color: #c0c4cc;
}

.pdf-placeholder p {
  margin: 8px 0;
  font-size: 14px;
}

.placeholder-tip {
  font-size: 12px !important;
  color: #c0c4cc !important;
}
</style>